.site {
  &-footer {
    background: $white;
    position: fixed;
    z-index: -1;
    bottom: 0;
    width: 100%;
    padding: 150px;
    &-content {
      h2 {
        font-size: 100px;
        color: #132c47;
        // font-family: $primary-font;
        letter-spacing: 10px;
        margin-bottom: 30px;
      }
      .action {
        color: $primary-color;
        position: relative;
        display: inline-block;
        z-index: 1;
        letter-spacing: 3px;
        font-size: 20px;
        &::before {
          content: "";
          position: absolute;
          width: 130%;
          height: 1px;
          background: $primary-color;
          z-index: -1;
          top: 50%;
          left: -15%;
          transform: scaleX(0);
          transform-origin: right;
          transition: transform 1s cubic-bezier(0.36, 0.03, 0, 0.91);
          transition-duration: 0.2s;
        }
        &:hover {
          text-decoration: none;
          &::before {
            transform: scaleX(1);
            transform-origin: left;
          }
        }
      }
    }
  }
}
