.site{
  &-article{
    padding: 200px 0 150px;
    background: $primary-color;
    &-header{
      @extend .site-project-alt-header;
      margin-bottom: 80px;
    }
    &-post{
      position: relative;
      margin-top: 80px;
      margin-left: 50px;
      margin-bottom: 50px;
      &-thumb{
        border-radius: 5px;
        overflow: hidden;
        img{
          display: block;
          width: 100%;
          transition: all .5s $site-ease;
        }
      }
      &-content{
        position: absolute;
        background: $white;
        border-radius: 5px;
        width: 90%;
        left: -7%;
        top: 0;
        transform: translateY(-50%);
        padding: 25px;
        transition: all .5s $site-ease;
        .svg-wrap{
          display: inline-block;
          position: absolute;
          right: 25px;
          top: 50%;
          transform: translateY(-50%);
          transition: all .3s $site-ease;;
          svg{
            width: 30px;
            height: 30px;
          }
        }
      }
      &-title{
        font-size: 25px;
        font-family: $secondary-font;
        // color: $text-color-dark;
        color: #222222;
        letter-spacing: 1px;
        display: block;
        margin-bottom: 5px;
        transition: all .3s $site-ease;
        &:hover{
          color: $primary-color;
          text-decoration: none;
        }
      }
      &-meta{
        font-size: 16px;
        letter-spacing: 1px;
        color: $text-color-light;
        span{
          font-family: $secondary-font;
        }
      }
      &:hover{
        .site-article-post-content{
          width: 100%;
          .svg-wrap{
            transform: translateY(-50%) rotate(-45deg);
          }
        }
        .site-article-post-thumb{
          img{
            transform: scale(1.2);
          }
        }
      }
    }
  }
}