.site {
  &-about {
    background: $primary-color;
    padding-top: 300px;
    padding-bottom: 100px;
    &-wrapper {
      position: relative;
    }
    &-image {
      width: 60%;
      border-radius: 5px;
      overflow: hidden;
      img {
        width: 100%;
      }
      .hotspot {
        position: absolute;
        top: 30px;
        left: 30px;
        z-index: 1;
        .icon {
          width: 30px;
          height: 30px;
          background: $primary-color;
          position: relative;
          cursor: pointer;
          border-radius: 3px;
          &::before,
          &::after {
            content: "";
            background: $white;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            width: 15px;
            height: 2px;
            transition: transform 0.5s $site-ease;
          }
          &::after {
            transform: translate(-50%, -50%) rotate(-270deg);
          }
        }
        .content {
          width: 220px;
          background: $white;
          padding: 20px;
          position: absolute;
          top: 0;
          left: 45px;
          transform: scaleX(0);
          transition: transform 0.5s $site-ease;
          transform-origin: left center;
          border-radius: 5px;
          h4,
          p {
            opacity: 0;
            transition: all 0.5s $site-ease;
          }
          h4 {
            font-size: 25px;
            color: #000648;
            font-family: $secondary-font;
            letter-spacing: 2px;
          }
          p {
            margin-bottom: 0;
            color: #000648;
            letter-spacing: 1px;
          }
        }
        &.active {
          .icon {
            &::after {
              transform: translate(-50%, -50%);
            }
          }
          .content {
            transform: scaleX(1);
            h4,
            p {
              opacity: 1;
            }
          }
        }
      }
    }
    &-content {
      color: $white;
      position: absolute;
      right: 0;
      width: 48%;
      top: 30px;
      h2 {
        font-size: 80px;
        letter-spacing: 10px;
      }
      p,
      a {
        font-size: 20px;
        letter-spacing: 2px;
        line-height: 26px;
      }
      p {
        margin-bottom: 30px;
      }
    }
  }
}
