.site-navigation{
  padding:10px 30px;
  background: transparent;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
  .navbar-brand{
    padding: 0;
    margin: 0;
    img{
      max-width: 78px;
      width: 78px;
      height: 78px;
      border-radius: 50%;
      box-shadow: 0 52px 64px 0 rgba(0,0,0,0.12);
    }
  }
  .nav-item{
    .nav-link{
      font-size: 18px;
      color: $white;
      transition: all .3s ease;
      padding: 15px 30px;
      letter-spacing: 3px;
      position: relative;
      &::before{
        content: "";
        position: absolute;
        width: 30%;
        height: 1px;
        background: $white;
        z-index: -1;
        top: 50%;
        left: 15%;
        transform: scaleX(0);
        transform-origin: right;
        transition: transform 0.5s ease-in-out;
        transition-duration: 0.2s;
      }
      &:hover{
        &::before {
          transform: scaleX(1);
          transform-origin: left;
        }
      }
    }
  }
  opacity: 0;
  transform: translateY(100px);
  transition: .9s $site-ease;
  transition-delay: .7s;
  &.animate {
    opacity: 1;
    transform: translateY(0);
  }
}