.btn-light {
  background: $white;
  box-shadow: 0 52px 64px 0 rgba(0, 0, 0, 0.12);
  border-radius: 5px;
  font-size: 20px;
  color: $primary-color;
  letter-spacing: 3px;
  padding: 15px 50px;
  transition: all 0.3s ease;
  transform-origin: center center;
  &:hover {
    color: $primary-color;
    background: $white;
  }
  &:active{
    color: $primary-color !important;
    background: $white !important;
  }
  &:focus{
    outline: 0;
    box-shadow: none !important;
  }
}

.site-btn {
  color: $white;
  position: relative;
  display: inline-block;
  z-index: 1;
  &::before {
    content: "";
    position: absolute;
    width: 80%;
    height: 1px;
    background: $white;
    z-index: -1;
    top: 50%;
    left: -15%;
    transform: scaleX(0);
    transform-origin: right;
    transition: transform 0.5s ease-in-out;
    transition-duration: 0.2s;
  }
  &:hover {
    text-decoration: none;
    color: inherit;
    &::before {
      transform: scaleX(1);
      transform-origin: left;
    }
  }
}
