.site {
  &-project {
    padding: 150px 0;
    background: $primary-color;
    margin-bottom: 500px;
    position: relative;
    z-index: 2;
    &-item {
      position: relative;
      &:not(:last-child) {
        margin-bottom: 150px;
      }
      &-thumb {
        width: 70%;
        overflow: hidden;
        position: relative;
        img {
          width: 100%;
          display: block;
          transition: transform 1s cubic-bezier(0.36, 0.03, 0, 0.91);
        }
      }
      &-content {
        position: absolute;
        top: 45%;
        left: 60%;
        max-width: 400px;
        z-index: 2;
        color: $white;
        .title {
          font-size: 80px;
          letter-spacing: 7px;
        }
        p,
        a {
          font-size: 20px;
          letter-spacing: 3px;
        }
        .action {
          color: $white;
          position: relative;
          display: inline-block;
          z-index: 1;
          &::before {
            content: "";
            position: absolute;
            width: 80%;
            height: 1px;
            background: $white;
            z-index: -1;
            top: 50%;
            left: -15%;
            transform: scaleX(0);
            transform-origin: right;
            transition: transform 0.5s ease-in-out;
            transition-duration: 0.2s;
          }
          &:hover {
            text-decoration: none;
            &::before {
              transform: scaleX(1);
              transform-origin: left;
            }
          }
        }
      }
      &:nth-child(even) {
        .site-project-item-thumb {
          margin-left: auto;
          &::before {
            left: 0;
            right: unset;
            transform-origin: left;
          }
        }
        .site-project-item-content {
          left: 20%;
        }
      }
      &:hover {
        .site-project-item-thumb {
          img {
            transform: scale(1.08) rotate(0.5deg);
          }
        }
      }
    }
  }
  &-project-alt{
    background: $primary-color;
    padding: 100px 0 250px;
    &-header{
      color: $white;
      width: 60%;
      h2 {
        font-size: 80px;
        letter-spacing: 10px;
      }
      p {
        font-size: 20px;
        letter-spacing: 2px;
        line-height: 35px;
      }
      margin-bottom: 50px;
    }
    &-wrapper{
      display: flex;
      flex-wrap: wrap;
      margin-top: 100px;
    }
    &-item{
      width: 45%;
      margin-bottom: 5%;
      .thumb{
        border-radius: 5px;
        overflow: hidden;
        img{
          display: block;
          width: 100%;
        }
        margin-bottom: 15px;
      }
      .content{
        display: flex;
        align-items: center;
        .left{
          width: 70%;
          color: $white;
          h4{
            font-family: $secondary-font;
            font-size: 35px;
            letter-spacing: 3px;
            margin-bottom: 0;
          }
          p{
            margin-bottom: 0;
            font-size: 18px;
            letter-spacing: 1px;
          }
        }
        a{
          flex: 1;
          text-align: right;
          display: block;
          .svg-wrap{
            transition: all .3s $site-ease;
            transform-origin: center center;
            display: inline-block;
            svg{
              path{
                fill: $white;
              }
            }
          }
          &:hover{
            .svg-wrap{
              transform: rotateZ(-45deg);
            }
          }
        }
      }
      &:nth-child(odd){
        margin-right: 5%;
      }
      &:nth-child(even){
        transform: translateY(20%);
      }
    }
  }
}

