.site{
  &-hero{
    height: 100vh;
    position: relative;
    z-index: 2;
    background-image: linear-gradient(1deg, #FFE9E2 38%, #EAA0D6 73%, #BB89DA 99%, #BA88DB 99%);
    overflow: hidden;
    .row{
      height: 100vh;
      align-items: center;
    }
    &-content{
      color: $white;
      .title{
        font-size: 80px;
        // font-weight: bold;
        line-height: 90px;
        margin-bottom: 80px;
        letter-spacing: 2px;
      }
      .action{
        position: relative;
        z-index: 3;
      }
    }
    &-image{
      margin-top: 100px;
    }
    &-shapes{
      position: absolute;
      width: 100%;
      z-index: 2;
      bottom: 0;
      .shape-one, .shape-two, .shape-three, .shape-four{
        position: absolute;
        width: 100%;
        height: auto;
        bottom: 0;
      }
      .shape-one{
        z-index: 6;
        transform: translateY(15%);
        path{
          fill: $primary-color;
        }
      }
      .shape-two{
        z-index: 5;
        transform: translateY(15%);
        path{
          fill: #B774DA;
        }
      }
      .shape-three{
        z-index: 4;
        transform: translateY(20%);
        path{
          fill: #BA88DB;
        }
      }
      .shape-four{
        z-index: 3;
        transform: translateY(25%);
      }
    }
  }
}