.site {
  &-team {
    padding: 100px 0 200px;
    background: $primary-color;
    &-header {
      color: $white;
      h2 {
        font-size: 80px;
        letter-spacing: 10px;
        margin-bottom: 40px;
      }
      p {
        font-size: 20px;
        letter-spacing: 2px;
        line-height: 26px;
      }
      margin-bottom: 90px;
    }
    &-row {
      display: flex;
      margin-bottom: 350px;
    }
    &-member {
      width: 45%;
      position: relative;
      &-image {
        img {
          width: 100%;
          display: block;
        }
        border-radius: 5px;
        overflow: hidden;
      }
      &-content {
        background: $white;
        position: absolute;
        width: 106%;
        bottom: -10%;
        left: 50%;
        transform: translateX(-50%);
        padding: 40px 50px 60px;
        color: #000648;
        border-radius: 3px;
        h5 {
          font-size: 25px;
          font-family: $secondary-font;
          letter-spacing: 2px;
          margin-bottom: 10px;
        }
        p {
          margin-bottom: 0;
          opacity: 0.65;
          letter-spacing: 2px;
        }
      }
      &:last-child {
        margin-left: auto;
        transform: translateY(300px);
      }
    }
  }
}
