html{
  scroll-behavior: smooth;
}

body {
  background: $primary-color;
  cursor: none;
}

main {
  background: $primary-color;
}

.preloader-wrap {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  bottom: 0;
  background: $primary-color;
  z-index: 2000;
  text-align: center;
  height: 100vh;
}

.percentage {
  z-index: 100;
  color: $white;
  opacity: 0.7;
  font-weight: 600;
  font-family: $secondary-font;
  font-size: 110px;
  letter-spacing: 5px;
  line-height: 130px;
  // margin-top: 40%;
  position: absolute;
  left: 50%; top: 40%;
  transform: translate(-50%, -50%);
}

.preloader-text {
  color: $white;
  font-size: 16px;
  letter-spacing: 2px;
  position: absolute;
  bottom: 60px;
  width: 100%;
  opacity: 0;
  line-height: 28px;
  transform: translate3d(0px, 20px, 0px);
  span {
    font-weight: 400;
    display: block;
  }
}

.main {
  opacity: 0;
  transform: translateY(100px);
  transition: .7s ease;
  transition-delay: .5s;
  overflow: hidden;
  &.animate {
    opacity: 1;
    transform: translateY(0);
  }
}

.magnetic {
  position: relative;
  z-index: 1000;
  padding: 50px;
  margin: -50px;
  display: inline-block;
}

.follower-wrapper{
  opacity: 0;
  transition: opacity 0.5s $site-ease;
  .pointer {
    position: fixed;
    z-index: 1000000;
    width: 10px;
    height: 10px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: -5px 0 0 -5px;
    background: rgba($color: $white, $alpha: .8);
    border-radius: 80%;
    backface-visibility: hidden;
    pointer-events: none;
    backface-visibility: hidden;
    transform-origin: center center;
    transition: background 0.5s $site-ease;
  }
  .follower{
    position: fixed;
    z-index: 1000001;
    width: 40px;
    height: 40px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: -5px 0 0 -5px;
    background: transparent;
    border: 1px solid rgba($color: $white, $alpha: 0.8);
    border-radius: 80%;
    backface-visibility: hidden;
    pointer-events: none;
    backface-visibility: hidden;
    // mix-blend-mode: difference;
    transform-origin: center center;
    transition: all 0.5s $site-ease;
    transition-property: transform, border;
    &.big {
      transform: translate(-50%, -50%) scale(1.5);
    }
  }
  &.revert{
    .pointer{
      background: rgba($color: #122C47, $alpha: 0.8);
    }
    .follower{
      border: 1px solid rgba($color: #122C47, $alpha: 0.8);
    }
  }
  &.active{
    opacity: 1;
  }
}

[data-scroll] {
  will-change: transform;
}
