.site {
  &-service {
    padding: 100px 0 200px;
    background: $primary-color;
    &-wrapper {
      position: relative;
    }
    &-header {
      position: relative;
      z-index: 1;
      color: $white;
      h2 {
        font-size: 80px;
        letter-spacing: 10px;
      }
      p {
        font-size: 20px;
        letter-spacing: 2px;
        line-height: 26px;
      }
      margin-bottom: 50px;
    }
    &-list {
      list-style: none;
      margin-left: 60px;
      &-item {
        cursor: pointer;
        margin-bottom: 0;
        padding: 10px 0;
        display: flex;
        align-items: center;
        img {
          margin-right: 20px;
        }
        span {
          color: $white;
          letter-spacing: 2px;
          font-size: 20px;
          transition: all 0.5s $site-ease;
        }
        &:hover {
          span {
            margin-left: 10px;
          }
        }
        &.visible {
          .site-service-image {
            visibility: visible;
            opacity: 1;
          }
        }
      }
    }
    &-image {
      position: absolute;
      width: 500px;
      height: 700px;
      border-radius: 5px;
      overflow: hidden;
      // background-image: url(../images/Img2.jpg);
      // background-size: cover;
      left: 90%;
      top: 0;
      visibility: hidden;
      opacity: 0;
      transition: all 0.5s $site-ease;
      img {
        display: none;
      }
      &.outside {
        visibility: hidden;
      }
    }
  }
}
